import React, { ChangeEvent, Component } from 'react';
import PropertyConfig from '../components/PropertyConfig/PropertyConfig';
import chartNames from './chart-names.json';
import appChartNames from './app-chart-names.json';
import properties from './chart-property-defs.json';
import colInfo from './chart-col-info.json';

export default class ChartProperties extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      selectedConfig: null,
      appChartName: null,
      colInfo: null,
    };
  }

  componentWillMount() {
    window.scrollTo(0, 0);
  }
  change(event: ChangeEvent<HTMLSelectElement>) {
    if (event.target.value !== 'select-one') {
      const chartDef = properties.find(
        chart => chart.chartType === event.target.value
      );
      const APP_CHART_NAMES: { [key: string]: string } = appChartNames;
      const COL_INFO: { [key: string]: any } = colInfo;
      this.setState({ 
        selectedConfig: chartDef,
        appChartName: APP_CHART_NAMES[event.target.value],
        colInfo: COL_INFO[event.target.value],
      });
    }
  }

  render() {
    let propConfig = null;
    if (this.state.selectedConfig) {
      propConfig = <PropertyConfig config={this.state.selectedConfig} appChartName={this.state.appChartName} colInfo={this.state.colInfo} />;
    }
    const CHART_NAMES: { [key: string]: string } = chartNames;
    const APP_CHART_NAMES: { [key: string]: string } = appChartNames;
    const COL_INFO: { [key: string]: any } = colInfo;
    const AVAILABLE_PROPERTIES = properties
      .map((property: any) => {
        property.chartName = CHART_NAMES[property.chartType];
        property.appChartName = APP_CHART_NAMES[property.chartType];
        if(property.chartType.indexOf('horiz') !== -1) {
          property.chartName += ' (Horizontal)';
        }
         if (!property.appChartName) {
           property.chartName = null;
         }
        return property;
      })
      .filter((prop: any) => !!prop.chartName)
      .sort((a, b) => {
        if (a.chartName < b.chartName) {
          return -1;
        }
        if (a.chartName > b.chartName) {
          return 1;
        }
        return 0;
      });
    return (
      <div className="page">
        <h2 className="display-2">Chart Specific Information</h2>
        <p className="body">
          Select a chart type to view chart properties and other information related to creating that chart. Not all chart types are currently supported.
        </p>
        <select onChange={this.change.bind(this)}>
          <option value="select-one">Select One</option>
          {AVAILABLE_PROPERTIES.map(prop => (
            <option key={prop.chartType} value={prop.chartType}>
              {prop.chartName || prop.chartType}
            </option>
          ))}
        </select>
        {propConfig}
      </div>
    );
  }
}
