import { PHOENIX_CHART_TYPE, PHOENIX_DATA_TYPE, PHOENIX_MAPPING } from '@domoinc/domo-phoenix';
import React, { Component } from 'react';
import { Chart } from '../components/Chart/Chart';
import CodeExample from '../components/CodeExample/CodeExample';
import { COMP, GAUGE, PROGRESS } from './examples';

const GAUGE_DATA = [
  ['Efficiency', 75]
];

const GAUGE_COLUMNS = [
  {
    type: PHOENIX_DATA_TYPE.STRING,
    name: 'Metric',
    mapping: PHOENIX_MAPPING.ITEM
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Percentage',
    mapping: PHOENIX_MAPPING.VALUE
  }
];

const PROGRESS_DATA = [
  ['Percent Complete', 75, 100]
];

const PROGRESS_COLUMNS = [
  {
    type: PHOENIX_DATA_TYPE.STRING,
    name: 'Label',
    mapping: PHOENIX_MAPPING.ITEM
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Percentage',
    mapping: PHOENIX_MAPPING.SERIES
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Max',
    mapping: PHOENIX_MAPPING.SERIES
  }
]

const COMP_GAUGE_DATA = [
  [0, 100, 63, 85]
];

const COMP_GAUGE_COLUMNS = [
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Min',
    mapping: PHOENIX_MAPPING.MIN
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Max',
    mapping: PHOENIX_MAPPING.MAX
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Previous',
    mapping: PHOENIX_MAPPING.PREVIOUS
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Current',
    mapping: PHOENIX_MAPPING.CURRENT
  }
]

export default class Gauge extends Component {
  componentWillMount() {
    window.scrollTo(0, 0);
  }

  render() {
    const gauge = GAUGE;
    const progress = PROGRESS;
    const comp = COMP;
    return (
      <div className="page">
        <h2 className="display-2">Gauges</h2>
        <p className="body">A gauge is a type of chart that shows you the current value compared to an existing max value. There are a couple types of gauge you can use.</p>

        <Chart type={PHOENIX_CHART_TYPE.FILLED_GAUGE} data={GAUGE_DATA} columns={GAUGE_COLUMNS}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <CodeExample example={gauge}></CodeExample>

        <h2 className="display-2" style={{marginTop: '60px'}}>Radial Gauge</h2>
        <p className="body">The radial gauge is the same as the filled gauge above just with a different appearance.</p>

        <Chart type={PHOENIX_CHART_TYPE.RADIAL} data={GAUGE_DATA} columns={GAUGE_COLUMNS}></Chart>
        <h4 className="display-4">Example Usage</h4>
        <p className="body">The same usage exactly as the gauge above, except you use <code>PHOENIX_CHART_TYPE.RADIAL</code> as your chart type.</p>

        <h2 className="display-2" style={{marginTop: '60px'}}>Comparative Gauge</h2>
        <p className="body">A special type of radial gauge that shows a current value on a range of a min to max value, and compares it with a given previous value.</p>

        <Chart type={PHOENIX_CHART_TYPE.COMPARITIVE_FILL_BASIC} data={COMP_GAUGE_DATA} columns={COMP_GAUGE_COLUMNS}></Chart>
        <h4 className="display-4">Example Usage</h4>
        <CodeExample example={comp}></CodeExample>

        <h2 className="display-2" style={{marginTop: '60px'}}>Progress Bar</h2>
        <p className="body">The progress bar is simply another way of displaying a current value in the context of a max value.</p>

        <Chart type={PHOENIX_CHART_TYPE.PROGRESS_BAR} data={PROGRESS_DATA} columns={PROGRESS_COLUMNS}></Chart>
        <h4 className="display-4">Example Usage</h4>
        <CodeExample example={progress}></CodeExample>
      </div>
    );
  }
}
