export const CREATE_CHART = `import { Component, OnInit } from '@angular/core';
import {
  PhoenixChart,
  PHOENIX_CHART_TYPE,
  PHOENIX_DATA_TYPE,
  PHOENIX_MAPPING
} from '@domoinc/domo-phoenix';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  constructor() {}

  ngOnInit() {
    const data = {
      rows: [
        ['Low', 'Corporate', 8582.8875],
        ['High', 'Home Office', 14415.941],
        ['Low', 'Consumer', 1264.8215],
        ['Medium', 'Small Business', 21478.799],
        ['Critical', 'Consumer', 2621.97],
        ['Not Specified', 'Consumer', 2211.31],
        ['Critical', 'Corporate', 10087.1315],
        ['Not Specified', 'Corporate', 4407.138],
        ['High', 'Consumer', 11667.366],
        ['High', 'Corporate', 19503.323],
        ['Low', 'Small Business', 1735.3715],
        ['Low', 'Home Office', 10057.42],
        ['Medium', 'Home Office', 7691.02],
        ['Critical', 'Small Business', 4036.064],
        ['Not Specified', 'Small Business', 84.99],
        ['High', 'Small Business', 689.74],
        ['Critical', 'Home Office', 7416.828],
        ['Not Specified', 'Home Office', 1839.26],
        ['Medium', 'Consumer', 4280.034],
        ['Medium', 'Corporate', 7965.238]
      ],
      columns: [
        {
          type: PHOENIX_DATA_TYPE.STRING,
          name: 'Order Priority',
          mapping: PHOENIX_MAPPING.SERIES
        },
        {
          type: PHOENIX_DATA_TYPE.STRING,
          name: 'Customer Segment',
          mapping: PHOENIX_MAPPING.ITEM
        },
        {
          type: PHOENIX_DATA_TYPE.DOUBLE,
          name: 'Sales',
          mapping: PHOENIX_MAPPING.VALUE
        }
      ]
    };

    // Chart Options
    const options = {
      width: 600,
      height: 500
    };

    // Create the Phoenix Chart
    const chart = new PhoenixChart(PHOENIX_CHART_TYPE.BAR, data, options);

    // Append the canvas element to your app
    document.getElementById('myDiv').appendChild(chart.canvas);

    // Render the chart when you're ready for the user to see it
    chart.render();
  }
}`;

export const SAMPLE_COMPONENT = `import {
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnInit
} from '@angular/core';
import {
  PhoenixChart,
  PHOENIX_CHART_TYPE
} from '@domoinc/domo-phoenix';

@Component({
  selector: 'app-phoenix-chart',
  templateUrl: './phoenix-chart.component.html',
  styleUrls: ['./phoenix-chart.component.scss']
})
export class PhoenixChartComponent implements OnInit, OnChanges {
  @Input() type = PHOENIX_CHART_TYPE.BAR;
  @Input() rows: any[];
  @Input() columns: any[];
  @Input() width = 600;
  @Input() height = 500;

  constructor(private elRef: ElementRef) { }

  ngOnInit() {
    this.render();
  }

  ngOnChanges() {
    this.render();
  }

  render() {
    const data = {
      rows: this.rows,
      columns: this.columns
    };

    const chart = new PhoenixChart(this.type, data, {
      width: this.width,
      height: this.height
    });

    this.elRef.nativeElement.appendChild(chart.canvas);

    chart.render();
  }
}`;

export const COMPONENT_USAGE = `<app-phoenix-chart [type]="myType" [rows]="myData" [columns]="myColumns"></app-phoenix-chart>`;
