import { PHOENIX_CHART_TYPE, PHOENIX_DATA_TYPE, PHOENIX_MAPPING } from '@domoinc/domo-phoenix';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import React, { Component } from 'react';
import { Chart } from '../components/Chart/Chart';
import CodeExample from '../components/CodeExample/CodeExample';
import { TRENDLINE } from './examples';

const data = [
  ['January', 8582.8875],
  ['February', 14415.941],
  ['March', 14264.8215],
  ['April', 21478.799],
  ['May', 15355.97],
  ['June', 12968.31],
  ['July', 19087.1315],
];

const columns = [
  {
    type: PHOENIX_DATA_TYPE.STRING,
    name: 'Date',
    mapping: PHOENIX_MAPPING.ITEM
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Sales',
    mapping: PHOENIX_MAPPING.VALUE
  }
];

export default class Line extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      showCurved: false
    };
  }

  componentWillMount() {
    window.scrollTo(0, 0);
  }

  showCurved(event: any) {
    this.setState({showCurved: event.target.checked});
  }

  render() {
    const example = TRENDLINE;
    // Define chart type based on showCurved
    let vertline = this.state.showCurved ? PHOENIX_CHART_TYPE.CURVED_LINE : PHOENIX_CHART_TYPE.LINE;
    let horizline = this.state.showCurved ? PHOENIX_CHART_TYPE.HORIZ_CURVED_LINE : PHOENIX_CHART_TYPE.HORIZ_LINE;

    return (
      <div className="page">
        <FormControlLabel
          className="page-switch"
          control={
            <Switch onChange={this.showCurved.bind(this)}></Switch>
          }
          label="Curved Style"
        ></FormControlLabel>
        <h2 className="display-2">Vertical Line Charts</h2>
        <p className="body">A basic form of line chart, this chart displays the data vertically on the y axis.</p>

        <Chart type={vertline} data={data} columns={columns}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <CodeExample example={example}></CodeExample>

        <h2 className="display-2" style={{marginTop: '60px'}}>Horizontal Line Charts</h2>
        <p className="body">This chart displays the data horizontally on the x axis rather than vertically.</p>

        <Chart type={horizline} data={data} columns={columns}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <p className="body">The same usage exactly as the bar chart above, except you use <code>PHOENIX_CHART_TYPE.HORIZ_LINE</code> or <code>PHOENIX_CHART_TYPE.HORIZ_CURVED_LINE</code> as your chart type.</p>
      </div>
    );
  }
}
