import { PHOENIX_CHART_TYPE, PHOENIX_DATA_TYPE, PHOENIX_MAPPING } from '@domoinc/domo-phoenix';
import React, { Component } from 'react';
import { Chart } from '../components/Chart/Chart';
import CodeExample from '../components/CodeExample/CodeExample';
import { VERT_BAR } from './examples';

const data = [
  ['Low', 'Corporate', 8582.8875],
  ['High', 'Home Office', 14415.941],
  ['Low', 'Consumer', 1264.8215],
  ['Medium', 'Small Business', 21478.799],
  ['Critical', 'Consumer', 2621.97],
  ['Not Specified', 'Consumer', 2211.31],
  ['Critical', 'Corporate', 10087.1315],
  ['Not Specified', 'Corporate', 4407.138],
  ['High', 'Consumer', 11667.366],
  ['High', 'Corporate', 19503.323],
  ['Low', 'Small Business', 1735.3715],
  ['Low', 'Home Office', 10057.42],
  ['Medium', 'Home Office', 7691.02],
  ['Critical', 'Small Business', 4036.064],
  ['Not Specified', 'Small Business', 84.99],
  ['High', 'Small Business', 689.74],
  ['Critical', 'Home Office', 7416.828],
  ['Not Specified', 'Home Office', 1839.26],
  ['Medium', 'Consumer', 4280.034],
  ['Medium', 'Corporate', 7965.238]
];

const columns = [
  {
    type: PHOENIX_DATA_TYPE.STRING,
    name: 'Order Priority',
    mapping: PHOENIX_MAPPING.SERIES
  },
  {
    type: PHOENIX_DATA_TYPE.STRING,
    name: 'Customer Segment',
    mapping: PHOENIX_MAPPING.ITEM
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Sales',
    mapping: PHOENIX_MAPPING.VALUE
  }
];

export default class Bar extends Component {
  componentWillMount() {
    window.scrollTo(0, 0);
  }

  render() {
    const example = VERT_BAR;
    return (
      <div className="page">
        <h2 className="display-2">Vertical Bar Charts</h2>
        <p className="body">A basic form of chart, this bar chart displays its data bars vertically.</p>

        <Chart type={PHOENIX_CHART_TYPE.BAR} data={data} columns={columns}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <CodeExample example={example}></CodeExample>

        <h2 className="display-2" style={{marginTop: '60px'}}>Horizontal Bar Charts</h2>
        <p className="body">This chart displays its data bars horizontally rather than vertically.</p>

        <Chart type={PHOENIX_CHART_TYPE.HORIZ_BAR} data={data} columns={columns}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <p className="body">The same usage exactly as the bar chart above, except you use <code>PHOENIX_CHART_TYPE.HORIZ_BAR</code> as your chart type.</p>

        <h2 className="display-2" style={{marginTop: '60px'}}>Grouped Bar Charts</h2>
        <p className="body">This chart displays its data bars side by side rather than stacked.</p>

        <Chart type={PHOENIX_CHART_TYPE.GROUPED_BAR} data={data} columns={columns}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <p className="body">The same usage exactly as the bar chart above, except you use <code>PHOENIX_CHART_TYPE.GROUPED_BAR</code> as your chart type.</p>

        <h2 className="display-2" style={{marginTop: '60px'}}>Nested Bar Charts</h2>
        <p className="body">This chart displays its data bars side by side with a total bar behind.</p>

        <Chart type={PHOENIX_CHART_TYPE.NESTED_BAR} data={data} columns={columns}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <p className="body">The same usage exactly as the bar chart above, except you use <code>PHOENIX_CHART_TYPE.NESTED_BAR</code> as your chart type.</p>

      </div>
    );
  }
}
