import 'normalize.css';
import React, { Component } from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';
import AngularIntegration from './Angular/Angular';
import ApiDoc from './ApiDoc/ApiDoc';
import './App.css';
import Area from './Area/Area';
import Bar from './Bar/Bar';
import ChartProperties from './ChartProperties/ChartProperties';
import { Charts } from './Charts/Charts';
import ScrollToTop from './components/ScrollToTop/ScrollToTop';
import Frameworks from './Frameworks/Frameworks';
import Gauge from './Gauge/Gauge';
import { Home } from './Home/Home';
import Line from './Line/Line';
import LineBar from './LineBar/LineBar';
import Lollipop from './Lollipop/Lollipop';
import MainMenu from './MainMenu/MainMenu';
import Maps from './Maps/Maps';
import Other from './Other/Other';
import Pie from './Pie/Pie';
import ReactIntegration from './React/React';
import Theming from './Theming/Theming';
import VueIntegration from './Vue/Vue';

class App extends Component {

  render() {
    return (
      <Router basename={process.env.PUBLIC_URL}>
        <div className="app">
          <MainMenu></MainMenu>
          <ScrollToTop>
            <div className="app-content" ref="ScrollDiv">
              <Route exact path="/" component={Home} />
              <Route exact path="/charts" component={Charts} />
              <Route exact path="/chart/bar" component={Bar} />
              <Route exact path="/chart/line" component={Line} />
              <Route exact path="/chart/linebar" component={LineBar} />
              <Route exact path="/chart/lollipop" component={Lollipop} />
              <Route exact path="/chart/area" component={Area} />
              <Route exact path="/chart/pie" component={Pie} />
              <Route exact path="/chart/map" component={Maps} />
              <Route exact path="/chart/gauge" component={Gauge} />
              <Route exact path="/chart/other" component={Other} />
              <Route exact path="/frameworks" component={Frameworks} />
              <Route exact path="/framework/angular" component={AngularIntegration} />
              <Route exact path="/framework/react" component={ReactIntegration} />
              <Route exact path="/framework/vue" component={VueIntegration} />
              <Route exact path="/properties" component={ChartProperties} />
              <Route exact path="/theming" component={Theming} />
              <Route exact path="/api" component={ApiDoc} />
              {/* <Redirect from="/" to="/home" /> */}
            </div>
          </ScrollToTop>
        </div>
      </Router>
    );
  }
}

export default App;
