import { PhoenixChart, PhoenixChartData, PhoenixChartOptions } from '@domoinc/domo-phoenix';
import React, { Component } from 'react';
import ReactDOM from 'react-dom';

export class Chart extends Component<any, any> {
  constructor(props: any) {
    super(props);
  }

  componentDidUpdate() {
    this.renderChart();
  }

  componentDidMount() {
    this.renderChart();
  }

  renderChart() {
    const options: PhoenixChartOptions = {
      width: this.props.width || 900,
      height: this.props.height || 400,
      properties: this.props.properties,
      colors: this.props.colors
    };
    const data: PhoenixChartData = {
      rows: this.props.data,
      columns: this.props.columns
    };
    const chart = new PhoenixChart(this.props.type, data, options);
    const el = ReactDOM.findDOMNode(this);
    if(el) {
      if(el.childNodes.length > 0 && el.firstChild) {
        // Trying to render new chart, remove old chart
        el.removeChild(el.firstChild);
      }
      el.appendChild(chart.canvas);
      chart.render();
    }
  }

  render() {
    return (
      <div></div>
    );
  }
}