import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import CodeExample from '../components/CodeExample/CodeExample';
import { CHART_PROPERTIES, PROPERTIES, RENDER, RESET_COLOR, RESIZE, UPDATE, UPDATE_OPTIONS } from './examples';

export default class ApiDoc extends Component {
  render() {
    return (
      <div className="page">
        <h2 className="display-2">Chart Options</h2>
        <p className="body">The following are customizable options along with their defaults:</p>
        <table className="table">
          <thead>
            <tr>
              <th>Property</th>
              <th>Description</th>
              <th>Type</th>
              <th>Default</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>width</td>
              <td>The width of the Phoenix Chart</td>
              <td>number</td>
              <td><code>500</code></td>
            </tr>
            <tr>
              <td>height</td>
              <td>The height of the Phoenix Chart</td>
              <td>number</td>
              <td><code>400</code></td>
            </tr>
            <tr>
              <td>animate</td>
              <td>Whether or not the chart should animate in when being drawn</td>
              <td>boolean</td>
              <td><code>true</code></td>
            </tr>
            <tr>
              <td>colors</td>
              <td>An array of hex codes to use in drawing charts. Overrides the default color palette.</td>
              <td>Array of strings</td>
              <td><code>null</code></td>
            </tr>
          </tbody>
        </table>

        <h2 className="display-2" style={{marginTop: '60px'}}>Chart Types</h2>
        <p className="body">We have provided an enum, <code>PHOENIX_CHART_TYPE</code>, for you with all the supported chart types. Note that many of the chart types are not documented yet, but we hope to eventually have all of them fully documented here. Here are a few examples of commonly used chart types:</p>
        <ul>
          <li className="body"><code>BAR</code> (Bar chart)</li>
          <li className="body"><code>STACKED_BAR</code> (Stacked bar chart)</li>
          <li className="body"><code>HORIZ_BAR</code> (Horizontal bar chart)</li>
          <li className="body"><code>LINE</code> (Line chart)</li>
          <li className="body"><code>CURVED_LINE</code> (Curved line chart)</li>
          <li className="body"><code>NAUTILUS</code> (Nautilus chart)</li>
          <li className="body"><code>PIE</code> (Pie chart)</li>
          <li className="body"><code>FUNNEL</code> (Funnel chart)</li>
          <li className="body"><code>BUBBLE</code> (Bubble chart)</li>
          <li className="body"><code>DONUT</code> (Donut chart)</li>
          <li className="body"><code>WORD_CLOUD</code> (Word cloud)</li>
        </ul>

        <h2 className="display-2" style={{marginTop: '60px'}}>Data Types</h2>
        <p className="body">We have provided an enum, <code>PHOENIX_DATA_TYPE</code>, for you with all the supported data types. Note that for the most part you'll be using <code>STRING</code>, <code>DOUBLE</code>, and <code>DECIMAL</code>. Here are the supported data types:</p>
        <ul>
          <li className="body"><code>STRING</code></li>
          <li className="body"><code>DOUBLE</code></li>
          <li className="body"><code>LONG</code></li>
          <li className="body"><code>DECIMAL</code></li>
          <li className="body"><code>DATE</code></li>
          <li className="body"><code>DATETIME</code></li>
          <li className="body"><code>TIME</code></li>
        </ul>

        <h2 className="display-2" style={{marginTop: '60px'}}>Mappings</h2>
        <p className="body">To correctly map your data to the chart, we require that you provide a mapping for your columns. We have an enum, <code>PHOENIX_MAPPING</code>, for the purpose of this mapping. The following are the mappings supported and their purpose:</p>
        <ul>
          <li className="body"><code>ITEM</code>: In a bar chart, this would be your x axis</li>
          <li className="body"><code>VALUE</code>: In a bar chart, this would be your y axis</li>
          <li className="body"><code>SERIES</code>: This is how your data is grouped</li>
          <li className="body"><code>CURRENT</code>: This is the current value (see <Link to="/chart/gauge">Gauges</Link>)</li>
          <li className="body"><code>PREVIOUS</code>: This is the previous value (see <Link to="/chart/gauge">Gauges</Link>)</li>
          <li className="body"><code>TARGET</code>: This is the target value, usually used to place a target marker on the graph</li>
          <li className="body"><code>MIN</code>: This is the minimum value (see <Link to="/chart/gauge">Gauges</Link>)</li>
          <li className="body"><code>MAX</code>: This is the maximum value (see <Link to="/chart/gauge">Gauges</Link>)</li>
          <li className="body"><code>BUBBLESIZE</code>: This is the value that is used to calculate the bubble size on a bubble chart</li>
          <li className="body"><code>XTIME</code>: This is the x axis on a scatterplot chart</li>
        </ul>

        <h2 className="display-2" style={{marginTop: '60px'}}>Chart Properties</h2>
        <p className="body">All Phoenix charts have default properties set to make your chart look great. You can, if you wish, override those properties. Examples of overrides include the chart's font size, whether or not to show the "Other" category, bar widths, etc. All usable chart properties are documented on <Link to="/properties">Chart Properties</Link>. You can use them like so:</p>
        <CodeExample example={CHART_PROPERTIES}></CodeExample>
        <p className="body">By passing those options, you'll have your chart customized to those settings.</p>

        <h2 className="display-2" style={{marginTop: '60px'}}>Chart Methods</h2>
        <p className="body">The following are the methods you have access to on a chart instance.</p>

        <h3 className="display-3" style={{marginTop: '40px'}}>render()</h3>
        <p className="body">This performs the actual rendering of the chart on the canvas. Your chart will not show up until you have called this method.</p>
        <CodeExample example={RENDER}></CodeExample>

        <h3 className="display-3" style={{marginTop: '40px'}}>resize(width, height)</h3>
        <p className="body">The resize method allows you to resize your chart to whatever size you want, given a width and a height. These are in numbers of pixels.</p>
        <CodeExample example={RESIZE}></CodeExample>

        <h3 className="display-3" style={{marginTop: '40px'}}>update(data, options?)</h3>
        <p className="body">The update method allows you to provide a new data object, which will update your chart to reflect those changes. NOTE: You do not need to call <code>render()</code> again, this method performs that for you.</p>
        <CodeExample example={UPDATE}></CodeExample>
        <p className="body">You may also <em>optionally</em> provide the options object to the update method. In this object you can pass an array of colors for a new color palette, as well as a map of chart property overrides, like so:</p>
        <CodeExample example={UPDATE_OPTIONS}></CodeExample>

        <h3 className="display-3" style={{marginTop: '40px'}}>setChartProperties(properties)</h3>
        <p className="body">You can pass your chart new properties any time and that will re-render your chart with those properties your provide. Simply pass an object of property keys with their values to the method.</p>
        <CodeExample example={PROPERTIES}></CodeExample>

        <h3 className="display-3" style={{marginTop: '40px'}}>resetColorPalette(properties)</h3>
        <p className="body">This method allows you to reset your chart's color palette back to the default Domo color palette. Your chart will automatically re-draw with the Domo color palette.</p>
        <CodeExample example={RESET_COLOR}></CodeExample>
      </div>
    );
  }
}