import { PHOENIX_CHART_TYPE, PHOENIX_DATA_TYPE, PHOENIX_MAPPING } from '@domoinc/domo-phoenix';
import React, { Component } from 'react';
import { Chart } from '../components/Chart/Chart';
import CodeExample from '../components/CodeExample/CodeExample';
import { PIE } from './examples';

const data = [
  ['Highly Recommend', 403],
  ['Recommend', 302],
  ['Neutral', 140],
  ['Do not recommend', 203],
  ['Unsure', 98],
];

const columns = [
  {
    type: PHOENIX_DATA_TYPE.STRING,
    name: 'Technology',
    mapping: PHOENIX_MAPPING.ITEM
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Store',
    mapping: PHOENIX_MAPPING.VALUE
  }
];

export default class Pie extends Component {
  componentWillMount() {
    window.scrollTo(0, 0);
  }

  render() {
    const example = PIE;
    return (
      <div className="page">
        <h2 className="display-2">Pie Charts</h2>
        <p className="body">A pie chart allows you to display percentages in an easy way, such as a breakdown of the types of reviews left by all reviewers.</p>

        <Chart type={PHOENIX_CHART_TYPE.PIE} data={data} columns={columns}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <CodeExample example={example}></CodeExample>

        <h2 className="display-2" style={{marginTop: '60px'}}>Nautilus Charts</h2>
        <p className="body">A nautilus chart has identical usage to a pie chart, but it adds in size as a method of displaying the data.</p>

        <Chart type={PHOENIX_CHART_TYPE.NAUTILUS} data={data} columns={columns}></Chart>
        <h4 className="display-4">Example Usage</h4>
        <p className="body">The same usage exactly as a pie chart, except you use <code>PHOENIX_CHART_TYPE.NAUTILUS</code> as your chart type.</p>

        <h2 className="display-2" style={{marginTop: '60px'}}>Donut Charts</h2>
        <p className="body">A donut chart has identical usage to a pie chart, it simply has a donut appearance.</p>

        <Chart type={PHOENIX_CHART_TYPE.DONUT} data={data} columns={columns}></Chart>
        <h4 className="display-4">Example Usage</h4>
        <p className="body">The same usage exactly as a pie chart, except you use <code>PHOENIX_CHART_TYPE.DONUT</code> as your chart type.</p>

        <h2 className="display-2" style={{marginTop: '60px'}}>Nautilus Donut Charts</h2>
        <p className="body">A nautilus donut chart has identical usage to a donut chart, but it adds in size as a method of displaying the data.</p>

        <Chart type={PHOENIX_CHART_TYPE.NAUTILUS_DONUT} data={data} columns={columns}></Chart>
        <h4 className="display-4">Example Usage</h4>
        <p className="body">The same usage exactly as a pie chart, except you use <code>PHOENIX_CHART_TYPE.NAUTILUS_DONUT</code> as your chart type.</p>

        <h2 className="display-2" style={{marginTop: '60px'}}>Funnel Charts</h2>
        <p className="body">The Funnel chart is used to visualize the progressive reduction of data as it passes from one phase to another.</p>

        <Chart type={PHOENIX_CHART_TYPE.FUNNEL} data={data} columns={columns}></Chart>
        <h4 className="display-4">Example Usage</h4>
        <p className="body">The same usage exactly as a pie chart, except you use <code>PHOENIX_CHART_TYPE.FUNNEL</code> as your chart type.</p>
      </div>
    );
  }
}
