export const US_MAP = `import {
  PhoenixChart,
  PHOENIX_CHART_TYPE,
  PHOENIX_DATA_TYPE,
  PHOENIX_MAPPING
} from '@domoinc/domo-phoenix';

const data = {
  // This is the data you get back from the Domo Data API
  rows: [
    ['Utah', 30000],
    ['California', 2000000],
    ['South Dakota', 2000],
    ['Illinois', 45000],
    ['Wisconsin', 76000],
    ['Washington', 1200000],
    ['Oregon', 70000]
  ],
  // You provide the names, types, and mappings of your ordered columns
  columns: [
    {
      type: PHOENIX_DATA_TYPE.STRING,
      name: 'State',
      mapping: PHOENIX_MAPPING.ITEM
    },
    {
      type: PHOENIX_DATA_TYPE.DOUBLE,
      name: 'Sales',
      mapping: PHOENIX_MAPPING.VALUE
    }
  ]
};

const options = { /* ... */ };

// Create the Phoenix Chart
const chart = new PhoenixChart(PHOENIX_CHART_TYPE.MAP_UNITED_STATES, data, options);`;

