import React, { Component } from 'react';
import propertyLabels from '../../ChartProperties/property-labels.json';

export default class Chart extends Component<any, any> {
  constructor(props: any) {
    super(props);
  }

  render() {
    const categoryMap: any = this.props.config.kpiMetaDataOverrides.reduce(
      (prev: any, cur: any) => {
        if (!prev[cur.category]) {
          prev[cur.category] = [];
        }
        if (!cur.deprecated) {
          if(cur.type !== 'select_list') {
            if(cur.type === 'boolean') {
              cur.valueList = ['true', 'false'];
            }
            if(cur.type === 'float') {
              cur.type = 'number';
            }
          }
          prev[cur.category].push(cur);
        }
        return prev;
      },
      {}
    );
    const categoriesArray = [];
    // Add categories to an iterable array
    for (const key in categoryMap) {
      if (categoryMap[key]) {
        if (key !== 'category__colors') {
          categoriesArray.push({
            category: key,
            properties: categoryMap[key]
          });
        }
      }
    }

    const optionalMappings = this.props.colInfo.filter((colItem : any) => !colItem.required);

const PROPERTY_LABELS: any = propertyLabels;
    return (
      <div className="chart-property-config">
        <h3 className="display-3">Enum Name</h3>
        <p className="body"><code>PHOENIX_CHART_TYPE.{this.props.appChartName}</code></p>
        <h3 className="display-3">Column Information</h3>


        <table className="table" style={{marginBottom: '40px'}}>
          <thead>
            <tr>
              <th>Mapping</th>
              <th>Required</th>
              <th>Supports formats</th>
              <th>Max possible</th>
            </tr>
          </thead>
          <tbody>
            {this.props.colInfo.filter((colItem : any) => colItem.required).map((colItem : any) => (
                <tr>
                  <td><code>{colItem.name}</code></td>    
                  <td>Yes</td>
                  <td>{colItem.formatting ? 'Yes' : 'No'}</td>
                  <td>{colItem.max}</td>
                </tr>
            ))}
            {this.props.colInfo.filter((colItem : any) => !colItem.required).map((colItem : any) => (
                <tr>
                  <td><code>{colItem.name}</code></td>    
                  <td>No</td>
                  <td>{colItem.formatting ? 'Yes' : 'No'}</td>
                  <td>{colItem.max}</td>
                </tr>
            ))}
          </tbody>
        </table>

        <h3 className="display-3">Chart Properties</h3>
        {categoriesArray.map((c: any) => (
            <div key={c.category}>
              <h4 className="display-4">{PROPERTY_LABELS[c.category]}</h4>
              <table className="table" style={{marginBottom: '40px'}}>
                <thead>
                  <tr>
                    <th>Property</th>
                    <th>Key</th>
                    <th>Description</th>
                    <th>Type</th>
                    <th>Acceptable Values</th>
                  </tr>
                </thead>
                <tbody>
                {c.properties.map((p: any) => (
                  <tr key={p.name}>
                    <td style={{width: '150px'}}>{PROPERTY_LABELS[`override__${p.name}__label`]}</td>
                    <td style={{width: '150px'}}><code>{p.name}</code></td>
                    <td dangerouslySetInnerHTML={p.tooltip ? {__html: PROPERTY_LABELS[p.tooltip].replace(/\n/g, '<br>')} : {__html: '<p class="secondary">No description</p>'}}></td>
                    <td style={{width: '100px'}}>{p.type}</td>
                    <td style={{width: '100px'}}>
                      {p.valueList && p.valueList.filter((v: any) => v.toLowerCase() !== 'default').map((val: any, index: any) => (
                        <div key={index}>
                          <code style={{margin: '5px 0'}}>{val}</code>
                        </div>
                      ))}
                    </td>
                  </tr>
                ))}
                </tbody>
              </table>
            </div>
          )
        )}
      </div>
    );
  }
}
