import { PHOENIX_CHART_TYPE, PHOENIX_DATA_TYPE, PHOENIX_MAPPING } from '@domoinc/domo-phoenix';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import React, { Component } from 'react';
import { Chart } from '../components/Chart/Chart';
import CodeExample from '../components/CodeExample/CodeExample';
import { AREA, PERCENT } from './examples';

const STACKEDTREND_DATA = [
  ['January', 'Store 4', 7256.23],
  ['January', 'Store 3', 5145.34],
  ['January', 'Store 2', 3250.00],
  ['January', 'Store 1', 4578.27],
  ['February', 'Store 4', 9956.09],
  ['February', 'Store 3', 9634.46],
  ['February', 'Store 2', 6672.67],
  ['February', 'Store 1', 7203.02],
  ['March', 'Store 4', 6592.02],
  ['March', 'Store 3', 7205.55],
  ['March', 'Store 2', 5123.14],
  ['March', 'Store 1', 3623.52],
  ['April', 'Store 4', 10283.26],
  ['April', 'Store 3', 9922.34],
  ['April', 'Store 2', 7123.00],
  ['April', 'Store 1', 5293.05]
];

const STACKEDTREND_COLUMNS = [
  {
    type: PHOENIX_DATA_TYPE.STRING,
    name: 'Date',
    mapping: PHOENIX_MAPPING.ITEM
  },
  {
    type: PHOENIX_DATA_TYPE.STRING,
    name: 'Store',
    mapping: PHOENIX_MAPPING.SERIES
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Sales',
    mapping: PHOENIX_MAPPING.VALUE
  }
];

const FULL_STACKEDTREND_DATA = [
  ['Q1', 'North', 10],
  ['Q1', 'South', 25],
  ['Q1', 'West', 38],
  ['Q1', 'East', 27],
  ['Q2', 'North', 12],
  ['Q2', 'South', 33],
  ['Q2', 'West', 29],
  ['Q2', 'East', 26],
  ['Q3', 'North', 12],
  ['Q3', 'South', 40],
  ['Q3', 'West', 26],
  ['Q3', 'East', 22],
  ['Q4', 'North', 18],
  ['Q4', 'South', 40],
  ['Q4', 'West', 11],
  ['Q4', 'East', 31]
];

const FULL_STACKEDTREND_COLUMNS = [
  {
    type: PHOENIX_DATA_TYPE.STRING,
    name: 'Quarter',
    mapping: PHOENIX_MAPPING.ITEM
  },
  {
    type: PHOENIX_DATA_TYPE.STRING,
    name: 'Region',
    mapping: PHOENIX_MAPPING.SERIES
  },
  {
    type: PHOENIX_DATA_TYPE.DOUBLE,
    name: 'Percentage Sales',
    mapping: PHOENIX_MAPPING.VALUE
  }
];

export default class Area extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      showCurved: false
    };
  }

  componentWillMount() {
    window.scrollTo(0, 0);
  }

  showCurved(event: any) {
    this.setState({showCurved: event.target.checked});
  }

  render() {
    const example = AREA;
    const percent = PERCENT;

    // Define chart type based on showCurved
    let stackedtrend = this.state.showCurved ? PHOENIX_CHART_TYPE.CURVED_STACKED_AREA : PHOENIX_CHART_TYPE.STACKED_AREA;
    let horizstackedtrend = this.state.showCurved ? PHOENIX_CHART_TYPE.HORIZ_CURVED_STACKED_AREA : PHOENIX_CHART_TYPE.HORIZ_STACKED_AREA;
    let percentstacked = this.state.showCurved ? PHOENIX_CHART_TYPE.PCT100_CURVED_STACKED_AREA : PHOENIX_CHART_TYPE.PCT100_STACKED_AREA;

    return (
      <div className="page">
        <FormControlLabel
          className="page-switch"
          control={
            <Switch onChange={this.showCurved.bind(this)}></Switch>
          }
          label="Curved Style"
        ></FormControlLabel>
        <h2 className="display-2">Vertical Stacked Area Charts</h2>
        <p className="body">A form of area chart, this area chart stacks the series vertically.</p>
        <Chart type={stackedtrend} data={STACKEDTREND_DATA} columns={STACKEDTREND_COLUMNS}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <CodeExample example={example}></CodeExample>

        <h2 className="display-2" style={{marginTop: '60px'}}>Horizontal Stacked Area Charts</h2>
        <p className="body">The same as the vertical stacked area chart, but this chart stacks the series horizontally along the x axis.</p>

        <Chart type={horizstackedtrend} data={STACKEDTREND_DATA} columns={STACKEDTREND_COLUMNS}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <p className="body">The same usage exactly as the bar chart above, except you use <code>PHOENIX_CHART_TYPE.HORIZ_STACKED AREA</code> as your chart type.</p>

        <h2 className="display-2" style={{marginTop: '60px'}}>100% Stacked Area Charts</h2>
        <p className="body">A variation of the stacked area chart, this chart fills 100% of the chart and breaks down your values into the percentage it should take.</p>

        <Chart type={percentstacked} data={FULL_STACKEDTREND_DATA} columns={FULL_STACKEDTREND_COLUMNS}></Chart>

        <h4 className="display-4">Example Usage</h4>
        <CodeExample example={percent}></CodeExample>
      </div>
    );
  }
}
